<template>
    <div class="mySong">
        <!-- 顶部导航栏 -->
        <van-nav-bar
            :title="music.name"
            left-arrow
            :border="false"
        >
        <template #left>
             <span class="iconfont icon-arrow-fine-up-copy" @click.stop="onClickLeft"></span>
        </template>
        </van-nav-bar>

        <!-- 中间歌词 -->
        <div ref="Bodys" class="Bodys">
            <van-tabs v-model="active" :sticky="true">
                <van-tab  title="歌词" v-if="!(this.lyrArr.length == 0)">
                    <div class="myDisks" ref="myDisks">
                      <div class="disks" ref="disks"  v-for="(item,index) in lyrArr" :key="index">
                          {{item[1]}}
                          <br v-if="item[1].length == 0"/>
                      </div>
                    </div>
                </van-tab>
                <van-tab title="封面">
                     <div ref="disk" class="disk" :style="{'background':'url('+music.imgUrl+')','animation-play-state' : (isState ? 'running' : 'paused') }"></div>
                </van-tab>
            </van-tabs>
        </div>

        
        <!-- 进度条 -->
        <div class="slider" v-show="isShow">
            <span>{{curTime}}</span>
            <van-slider button-size="14px" drag-start="gaiPlay(false)" drag-end="gaiPlay(true)" active-color="#ffffff" inactive-color="#cccccc"  v-model="value" @change="onChange" />
            <span>{{durTime}}</span>
        </div>
        


        <!-- 底部播放器 -->
        <div class="plays">
           <div>
               <span class="iconfont icon-yule_shangyishou" @click="ff_queueIndex(-1)"></span>
               <span class="iconfont" @click="gaiPlay(!music.isPlay)" :class="music.isPlay ? 'icon-pause' : 'icon-bofangjian'"></span>
               <span class="iconfont icon-yule_xiayishou"  @click="ff_queueIndex(1)"></span>
           </div>
            <van-cell  @click="showPopup">
                <template>
                    <span class="iconfont icon-bofangliebiao"></span>
                </template>
            </van-cell>

        </div>


          <!-- 队列弹窗 -->
            <van-popup v-model="show">
                <van-icon name="cross" @click="ff_show"/>
                <div class="bodys" >
                    <ul>
                        <li  class="danSong" v-for="(Item,index) in newQueue" :key="index">
                            <span>{{index-0+1}}</span>
                            <div class="gequ" @click="ff_queueIndex(index)">
                                <p>{{Item.songName}}</p>
                                <p>
                                    <span>独家</span>
                                    <span>{{Item.autName}}</span>
                                    <span>-{{Item.songBrief}}</span>
                                </p>
                            </div>
                            <span class="iconfont icon-iconfontlajitong" @click="rovemItem(index,Item.songName)"></span>
                        </li>
                    </ul>
                </div>
            </van-popup>

       
        <img :src="music.imgUrl"/>
        <div class="Screen"></div>
    </div>
</template>

<script>
    import '../assets/less/Song.less'
    export default {
        name : 'Song',
        data(){
            return{ 
                  isOptical : false,
                  active    : 0,
                  lyrArr    : [],
                  isState   : true,
                  value     : 0,
                  curTime   : null,
                  durTime   : null,
                  offset    : -40, //滚动距离（应等于行高）
                  isShow    : true,
                  show      : false,
                  newQueue  : [],

                  
            }
        },
        //   if(!this.newQueue.length){
        //         this.newQueue = JSON.parse(sessionStorage.getItem('queue'));
        //         console.log( '队列', JSON.parse(sessionStorage.getItem('queue')))
        //     }
        created() {          
            this.$toast.loading({
                message: '获取中...',
                forbidClick: true,
                loadingType: 'spinner',
            });
            this.newQueue = this.queue;
            if(!this.music.isPlay){
                this.isState = false;
                this.isShow = false;
            }
           
        



            if(!this.music.url){
                // 将字符串转为dom元素
                function wrapper(DomStr){
                    let wrapper= document.createElement('div');
                    wrapper.innerHTML = DomStr;
                    let div = wrapper.firstChild;
                    wrapper = null;
                    return div
                }             



                  let a = JSON.parse(sessionStorage.getItem('song'));
                  this.music.id          = a.id;
                  this.music.imgUrl      = a.imgUrl;
                  this.music.myAudio     = wrapper(a.myAudioStr);
                  this.music.name        = a.name;
                  this.music.url         = a.url;
                  
                  console.log(wrapper(a.myAudioStr));
                  console.log('歌词界面的music对象',this.music)
            }


            // 隐藏播放器
            this.ff_gaiIsflash(false)
            
            //  解析歌词
            this.ff_Lyric(this.$route.query.id)

            // 歌词滚动
            this.ff_justice();

            let queue = JSON.parse(sessionStorage.getItem('queue'));
            this.newQueue  = queue;
            this.$store.commit('gaiQueue',queue);
        
            
            
        },
        computed: {
            music(){
                   return this.$store.state.song ;
            },
            lyric(){
                   return this.$store.state.lyric;
            },
            queueIndex(){
                   return this.$store.state.queueIndex;
            },
            queue(){
                   return this.$store.state.queue;
            }
            
        },
        methods: {
        // 进度条
        onChange(){
           
            let myDisks  = this.$refs.myDisks;
            let disks    = this.$refs.disks;
            this.isState = true;
            this.music.myAudio.currentTime = Math.floor(((this.value-0)/100) * (this.music.myAudio.duration-0));
            
            // 改变高亮的行数
            let arr01 = this.lyrArr.filter((item,index)=>{
                return item[0] >= this.music.myAudio.currentTime
            });
            console.log('arr01',arr01)
            let lineNo = null;
        
            if(arr01.length > 0){
                this.lyrArr.forEach((item,index)=>{
                    if(item[0] == arr01[0][0]){
                        this.lyric.lineNo = index;
                    }
            })

            }
            
  
            


            // 将装歌词的盒子移动到高亮的位置
            if(myDisks){
                 this.$refs.myDisks.style.transform = "translateY("+(195+(this.lyric.lineNo-0 - (this.lyric.C_pos-0))*this.offset)+"px)"; 
            }
           
            // 先清除所有带有高亮的类名
            let lineHighs =  document.querySelectorAll(".lineHigh");
            if(lineHighs.length != 0){
                 for(let i=0;i<lineHighs.length;i++){
                    lineHighs[i].className = 'disks';
               }
            }
             
            // 再给现在的高亮行数dom赋高亮的样式
            if(disks &&  this.$refs.disks[this.lyric.lineNo]){
                    this.$refs.disks[this.lyric.lineNo].classList.add("lineHigh")
            }
            
           if(this.music.myAudio){
                //播放器时间
                this.curTime =  this.ff_time(this.music.myAudio.currentTime);


           }
           
        },
        // 获取歌词
        ff_Lyric(id){
            this.axios({
                url : '/lyric',
                method:'GET',
                params:{
                    id
                }

            })
            .then(res=>{
                if(res.data.code == 200){
                    //  console.log(res.data.lrc.lyric)
                  this.lyrArr =  this.ff_lyricChu(res.data.lrc.lyric);
                  console.log(this.lyrArr)
                  this.$toast.clear();
                                 
                }
               
            })
            .catch(err=>{
                console.log(err)
            })
        },
        // 离开歌词界面
        onClickLeft(){
            
            //  先解除对播放器的事件监听
            if(this.music.myAudio){
                this.music.myAudio.ontimeupdate = null;
                this.music.myAudio.onended = null;

            }
             
            //  再返回上一级
             this.$router.go(-1);
            //  再显示出播放器
             this.music.isFlash = true;
            

        },
        // 解析歌词
        ff_lyricChu(lrcJSON){
                
       
            //先按行分割
            let lyric = lrcJSON.split('\n'); 
            //新建一个数组存放最后结果
            let lrc = [];
            let _l = lyric.length;
            for(let i=0;i<_l;i++) {
                //正则匹配播放时间返回一个数组
                let sj = lyric[i].match(/\[\d{2}:\d{2}((\.|\:)\d{2})\]/g);
                //获得该行歌词正文 
                let _lrc = lyric[i].replace(/\[\d{2}:\d{2}((\.|\:)\d{2})\]/g,"");
                //过滤掉空行等非歌词正文部分
                if(sj != null) { 
                    //可能有多个时间标签对应一句歌词的情况，用一个循环解决
                    let _ll = sj.length;
                    for(let j=0;j<_ll;j++){
                        let _s=sj[j];
                        let min = Number(String(_s.match(/\[\d{2}/i)).slice(1));
                        let sec = parseFloat(String(_s.match(/\d{2}\.\d{2}/i)));
                        //换算时间，保留两位小数
                        let _t = Math.round((min * 60 + sec)*100)/100;
                        //把时间和对应的歌词保存到数组
                        lrc.push([_t,_lrc]);
                    }
                }
            }
          
           return lrc;

        },
        // 隐藏播放器
        ff_gaiIsflash(data){
            this.$store.dispatch('gaiIsflash',data)
        },
        // 歌词滚动
        ff_justice(){
            //播放器
            let audio = this.music.myAudio;
            document.querySelectorAll('.lineHigh').forEach(item=>{
                item.classList.remove("lineHigh");
            })

            //监听播放器的timeupdate事件，实现文字与音频播放同步
            if(audio){
              audio.ontimeupdate = ()=>{

                 //播放器时间
                this.curTime =  this.ff_time(audio.currentTime);
                 //播放时长
                this.durTime =  this.ff_time(audio.duration);

                 // 滑块进度
                // console.log(((audio.currentTime-0) / (audio.duration-0)) * 100 )
                let value = ((audio.currentTime-0) / (audio.duration-0)) * 100 ;
                // value = value >= 97 ? 100 : value;
                this.value = value;
                
                audio.onended =  ()=>{
                    this.isState = false;
                }
                if(this.lyric.lineNo>=this.lyrArr.length){
                    return;
                }
                   
                

                 
                console.log(this.music.myAudio && this.lyrArr[this.lyric.lineNo],'ahidw')
               if(this.music.myAudio && this.lyrArr[this.lyric.lineNo]){
                    console.log()
                    console.log(this.music.myAudio.currentTime)
                     if(parseFloat(this.lyrArr[this.lyric.lineNo][0])<=this.music.myAudio.currentTime){
                    
                    //歌词数组
                    let lis = this.$refs.disks;

                    if(this.lyric.lineNo>=0){
                        //去掉上一行的高亮样式
                        if(lis[this.lyric.lineNo-1]){
                            lis[this.lyric.lineNo-1].classList.remove("lineHigh");
                        }
                        
                    }
                    if(this.lyric.lineNo>=0){
                        //高亮显示当前行
                       lis[this.lyric.lineNo].classList.add("lineHigh");

                    }
                    

                    //文字滚动
                    if(this.lyric.lineNo>=this.lyric.C_pos){

                        //整体向上滚动一行高度
                        this.$refs.myDisks.style.transform = "translateY("+(195+(this.lyric.lineNo-0 - this.lyric.C_pos-0)*this.offset)+"px)"; 
                    }
                       //显示下一行高亮
                      this.gaiLyric(1);
                       }

                        //监听播放器的ended事件，播放结束时回滚歌词
                 console.log(audio)
                    audio.onended =  ()=>{

                        
                        console.log("歌曲结束")
                        // 隐藏播放器
                        this.ff_gaiIsflash(false)

                        //回滚歌词
                        let lineHight = document.querySelector(".lineHigh");

                        if(lineHight){
                            lineHight.classList.remove("lineHigh");
                        }
                        if(this.$refs.myDisks){
                            this.$refs.myDisks.style.transform = "translateY(195px)";
                        }
                    
                        this.lyric.lineNo = 0;
                        console.log('this.lyric.lineNo',this.lyric.lineNo)
                        
                        //暂停歌曲
                        this.gaiPlay(false);

                        
                        
                    };

               }else{
               
              

               }

               }
           
              
              
               
               
            }
           

           
        },
        // 改变歌词高亮的行数
        gaiLyric(data){
            this.$store.dispatch('gaiLyric',data)
        },
        // 改播放状态
        gaiPlay(data){
              console.log(data,'触发了123')
             this.$store.dispatch('gaiPaly',data);
            
             if(this.music.isPlay){
                 console.log(this.music.myAudio)
               if(this.music.myAudio){
                    this.music.myAudio.play(); 
               }
                
                this.isState = true;  
                 console.log(this.isState,'触发了')
                this.isShow = true;
               
            }else{
                if(this.music.myAudio){
                    this.music.myAudio.pause();
                }
                
                this.isState = false; 
                this.isShow = false;
                
            }  
            
        },
        // 时间处理
        ff_time(clock){
                //分钟
                let minute = clock / 60;
                let minutes = parseInt(minute);
                if (minutes < 10) {
                    minutes = "0" + minutes;
                }

                //秒
                let second = clock % 60; 
                let seconds = Math.floor(second);
                if (seconds < 10) {
                    seconds = "0" + seconds;
                }
                let clockStr = `${minutes}:${seconds}`
                return clockStr;
        },
         // 修改播放队列的序号
        ff_queueIndex(data){
            if(this.isIndex){
                console.log('防抖了')
                clearTimeout(this.isIndex);
            }
            

            this.isIndex =  setTimeout(()=>{
                 //  解析歌词
            this.ff_Lyric(this.$route.query.id)

            // 歌词滚动
            this.ff_justice();
            if(this.newQueue.length >=2){
                    console.log('暂停了')
                    console.log(this.music)
                    if(this.music.myAudio){
                             this.music.myAudio.pause();
                    }
                    
                    this.isState = true;
                    this.lyric.lineNo = 0;
                    let line = document.querySelector('.lineHigh')
                    if(line){
                        line.classList.remove('.lineHigh')
                    }

                    console.log('flash中的序号',data)
                    this.$store.dispatch('gaiIndex',data).then(res=>{
                        console.log('开启')
                        
                         if(this.music.myAudio){
                             this.music.myAudio.play();
                              
                         }
                        
                        console.log(res[this.queueIndex])
                        this.ff_songs(res[this.queueIndex].songId,res[this.queueIndex].songName,res[this.queueIndex].songUrl); 


                       
                    });

            }else{
                this.$toast('暂无歌曲')
            }

            },1000)

            
           
        },
         // 获取歌曲url
        ff_songs(id,name,imgUrl){
           
             if(this.music.myAudio != "undefined"){
                    this.music.myAudio.pause();
            }

            this.$toast.loading({
                message: '获取中...',
                forbidClick: true,
                loadingType: 'spinner',
            });
                this.axios({
                    url : '/song/url',
                    method : 'GET',
                    params : {
                        id
                    }
                })
                .then(res=>{
                    console.log(res)
                    if(res.data.code == 200){
                        let a = {};
                        
                        a.name     = name;
                        a.url      = res.data.data[0].url;
                        a.imgUrl   = imgUrl;
                        a.id       = id;
                        a.isFlash  = true;
                        a.isPlay   = true;
                        this.musicObj = a;
                        this.$store.dispatch('gaiMusic',this.musicObj);
                           if(this.music.myAudio != "undefined"){
                                    this.music.myAudio.play();
                            }
                        
                        this.$toast.clear();
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
        },
         // 显示
        showPopup() {
               let queue = JSON.parse(sessionStorage.getItem('queue'));
               if(queue.length >= 1){
                    this.show = true;
                    this.newQueue  = queue;

                    this.$store.commit('gaiQueue',queue)

                    
               }else{
                   this.$toast('请添加播放歌曲进队列')
               }
                   

               
        },
        ff_show(){
            this.show = false;
        },
        ff_a(){
            this.pause();
            this.ff_queueIndex(1);
        },
        // 删除队列的一项
        rovemItem(data,songName){
            this.$dialog.confirm({
                    message: `确认将“${songName}”移除`,
                })
                .then(() => {
                    let quese =  this.queue.splice(data,1);
                   
                    window.sessionStorage.setItem('queue',JSON.stringify(this.queue))
                    

                    this.$toast('移除成功')
                    if(this.queue.length <= 0){
                             this.show = false;
                    }
                })
                .catch(() => {
                    // on cancel
                });
            
        }
        },
        
        watch:{
            music(news,old){
                if(news.isFlash){
                       
                       news.isFlash = false;
                       if(this.queue.length >= 2){
                            this.isShow  = true;
                            this.isState = true;
                       }else{
                           this.gaiPlay(false)
                       }
                       
                       
                    //    this.isState = !this.isState;
                      //  解析歌词
                       console.log(this.queue[this.queueIndex].songId)
                       this.ff_Lyric(this.queue[this.queueIndex].songId)

                       // 歌词滚动
                       this.ff_justice();
                       console.log(this.queue[this.queueIndex],'略略略')
                     
                       console.log(news.isFlash,'新的isFlash')
                       console.log('新的歌词',this.lyrArr)
                }
               
               
            }
        }
        
        
    }
</script>

<style lang="less" scoped>

</style>